import { Switch, Route } from 'react-router-dom';
import './App.scss';
import './fonts.scss';
import Home from './Pages/Home/Home';
import Chanel from './Pages/Chanel/chanel';
import Videos from './Pages/videos/video';
// import Users from './Pages/Users/Users';

// ooooooooooooo
import Library from './Pages/library/library';
import Games from './Pages/library/Games';
import Music from './Pages/library/Music';
import Watch from './Pages/library/watch';
import History from './Pages/library/history';
import Fovourites from './Pages/library/fovourites';
import Liked from './Pages/library/liked';
import About from './Pages/library/About'
import Discussion from './Pages/library/Discussion'
import Playlists from './Pages/library/Playlists'
// 0000000000000


function App() {
  return (<>

      <Switch>
        <Route path="/" component={Home} exact/>
      <Route path="/videos" component={Videos} exact />
        <Route path="/videos/:id" component={Videos} exact/>
      <Route path="/chanel" component={Chanel} exact />
      {/* //////////////// */}
      <Route path="/library" component={Library} exact />
      <Route path="/games" component={Games} exact />
      <Route path="/music" component={Music} exact />
      <Route path="/watch" component={Watch} exact />
      <Route path="/history" component={History} exact />
      <Route path="/favourites" component={Fovourites} exact />
      <Route path="/liked" component={Liked} exact />

      {/* /////////////// */}
      <Route path="/about" component={About} exact />
      <Route path="/discussion" component={Discussion} exact />
      <Route path="/playlist" component={Playlists} exact />

    {/* ////////////////// */}
      </Switch>
    
    </>);
}

export default App;
